import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { GlobalStyle } from './styles/GlobalStyle';
import { Modal } from './components/Modal';
import { HeroDetails } from './components/HeroDetails';
import { CookieSide } from './components/CookieSide';
import { DetailsBox } from './components/DetailsBox';
import { ShareButton } from './components/ShareButton';
import { useTelegram } from './hooks/useTelegram';
import { useStorage } from './hooks/useStorage';
import { translations } from './locales';

const ContentPage = styled.div`
  width: 100%;
  height: 100%;
  background: radial-gradient(91.35% 50% at 50% 50%, #FAEFCE 0%, #EECC91 100%);
  display: flex;
  padding: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  flex: 1 0 0;
  align-self: stretch;
  margin: auto;
  overflow: hidden;
  position: relative;
`;

function App() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [prediction, setPrediction] = useState('');
  const [isOpened, setIsOpened] = useState(false);
  const [hasClosedFirstTime, setHasClosedFirstTime] = useState(false);
  const [language, setLanguage] = useState<'en' | 'ru' | 'uk'>('ru');
  
  const { onBuy, onShare, tg } = useTelegram();
  const storage = useStorage();
  const t = translations[language];

  useEffect(() => {
    // Проверяем язык пользователя
    const userLanguage = tg?.initDataUnsafe?.user?.language_code || 'ru';
    setLanguage(userLanguage as 'en' | 'ru' | 'uk');

    // Проверяем сохраненное предсказание
    if (storage.isPredictionValid()) {
      const savedPrediction = storage.getItem('prediction');
      if (savedPrediction) {
        setPrediction(savedPrediction);
        setIsOpened(true);
        setHasClosedFirstTime(true);
      }
    } else {
      storage.clearPrediction();
    }
  }, []);

  const handleModalClose = () => {
    setIsModalOpen(false);
    if (!hasClosedFirstTime) {
      setHasClosedFirstTime(true);
      onShare();
    }
  };

  const handleCookieClick = async () => {
    if (!isOpened) {
      const isTelegram = !!tg;
      console.log('Is Telegram environment:', isTelegram);
      console.log('Telegram object:', tg);
      
      if (isTelegram) {
        try {
          console.log('Starting payment process...');
          const result = await onBuy();
          console.log('Payment result:', result);
          
          if (!result) {
            console.log('Payment cancelled or failed');
            return;
          }
          
          // Если оплата успешна
          console.log('Payment successful, showing prediction...');
          setIsOpened(true);
          setTimeout(() => {
            const newPrediction = 'В 2025 году вас ждет большое достижение — верьте в себя и двигайтесь вперед!';
            setPrediction(newPrediction);
            storage.setItem('prediction', newPrediction);
            storage.setItem('purchaseDate', new Date().toISOString());
            setIsModalOpen(true);
          }, 2000);
        } catch (err) {
          const error = err as Error;
          console.error('Error during purchase:', error);
          console.log('Error details:', {
            name: error?.name || 'Unknown error',
            message: error?.message || 'No message',
            stack: error?.stack || 'No stack trace'
          });
        }
      } else {
        console.log('Not in Telegram environment, skipping payment');
      }
    } else {
      setIsModalOpen(true);
    }
  };

  return (
    <>
      <GlobalStyle />
      <ContentPage>
        <Modal 
          isOpen={isModalOpen} 
          onClose={handleModalClose}
          prediction={prediction}
        />
        <HeroDetails />
        <CookieSide 
          onClick={handleCookieClick}
          isOpened={isOpened}
        />
        {!hasClosedFirstTime ? (
          <DetailsBox
            icon="tap_icon.svg"
            text={t.tapToBuy}
          />
        ) : (
          <DetailsBox
            icon="clover_icon.svg"
            text={t.comeBack}
          />
        )}
        <ShareButton onClick={onShare} />
      </ContentPage>
    </>
  );
}

export default App;
