declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = typeof window !== 'undefined' ? window.Telegram?.WebApp : null;

export function useTelegram() {
  const onClose = () => {
    tg?.close();
  };

  const onShare = () => {
    if (tg) {
      tg.switchInlineQuery('Получи своё предсказание на сегодня! 🥠');
    } else {
      console.log('Share clicked (browser mode)');
    }
  };

  const onBuy = async () => {
    console.log('Starting payment flow...');
    console.log('Telegram object:', tg);
    
    if (!tg) {
      console.error('Telegram WebApp is not available');
      return false;
    }
    
    try {
      console.log('Showing payment popup...');
      const result = await tg.showPopup({
        title: 'Покупка предсказания',
        message: 'Стоимость предсказания - 1 Star',
        buttons: [
          {
            type: 'buy',
            text: 'Купить за 1 Star',
            params: {
              amount: 1,
              currency: 'STARS'
            }
          },
          { type: 'cancel', text: 'Отмена' }
        ]
      });

      console.log('Popup result:', result);
      return result?.button_id === 'buy';
    } catch (err) {
      const error = err as Error;
      console.error('Payment error:', error);
      console.log('Error details:', {
        name: error?.name || 'Unknown error',
        message: error?.message || 'No message',
        stack: error?.stack || 'No stack trace'
      });
      return false;
    }
  };

  return {
    onClose,
    onShare,
    onBuy,
    tg,
    user: tg?.initDataUnsafe?.user,
  };
}
